import React from 'react'
import ReactTooltip from 'react-tooltip'

const defenitions = [
  {
    title: 'react-native',
    def:
      'React native is a cross-platform tool for developing native mobile applicitions'
  },
  {
    title: 'javascript',
    def: 'well javascript is just javascript'
  }
]
export default function Stack ({
  title,
  src,
  containerStyle,
  id,
  tooltip = true
}) {
  const getDef = defTitle => {
    defTitle = defTitle.toLowerCase()
    let def = defenitions.filter(item => {
      if (item.title === defTitle) {
        return true
      }
      return false
    })
    if (def.length > 0) return def[0].def
    else return null
  }
  return (
    <>
      <ReactTooltip
        id={'getContent' + id}
        multiline
        effect='solid'
        place='bottom'
        getContent={() => getDef(title)}
      />
      <p
        data-for={'getContent' + id}
        data-tip
        className='stack-item'
        style={containerStyle}
      >
        <img height='20px' width='20px' src={src} alt={"stack item "+title }/>
        <span>{title}</span>
      </p>
    </>
  )
}
