import React from 'react'
import { styled } from '@mui/material/styles'

import TextField from '@mui/material/TextField'
const CssTextField = styled(TextField)({
  '&': {
    width: '100%'
  },
  '& .MuiInput-input': {
    color: 'var(--cream)',
    fontFamily: 'Fira Sans',
    fontSize: '1.125em'
  },
  '& label.Mui-focused': {
    color: 'var(--cream)',
    fontWeight: 500,
    fontFamily: 'Fira Sans'
  },
  '& .MuiInput-underline:before': {
    borderBottomColor: 'var(--cream)'
  },
  '& .MuiInput-root:hover:not(.Mui-disabled):before': {
    borderBottomColor: 'var(--cream)'
  },
  '& .MuiInput-underline:after': {
    borderBottomColor: 'var(--main)'
  },
  '& .MuiInputLabel-root': {
    color: 'var(--cream)',
    fontSize: '1.125em',
    fontWeight: 500,
    fontFamily: 'Fira Sans'
  }
})
export default function Input ({ label, value, onChange,id, name }) {
  return (
    <CssTextField
      id='custom-css-outlined-input'
      label={label}
      value={value}
      onChange={onChange}
      variant='standard'
      autoComplete='off'
      id={id}
      name={name}
    />
  )
}
