import React from 'react'
import Loader from 'react-loader-spinner'

export default function Button ({
  text,
  containerStyle,
  onPress,
  disabled,
  loading = false
}) {
  return (
    <a
      onClick={() => onPress()}
      className='outline-button'
      style={containerStyle}
      disabled={disabled}
    >
      <span>{text}</span>
      {loading && (
        <Loader
          type='TailSpin'
          color='var(--cream)'
          height={20}
          width={20}
          ariaLabel='loading-indicator'
          style={{ marginLeft: '.5em' }}
          visible={true}
        />
      )}
    </a>
  )
}
