import React from 'react'
import { Link } from 'gatsby'
export default function Footer ({ navbarItems = [] }) {
  return (
    <section id='footer' className='footer section-spacing'>
      <div className='footer__element'>
        <h3>Contact Information</h3>
        <a
          href='mailto:amrirachid15@gmail.com'
          className='footer__element__icon-text'
        >
          <p>amrirachid15@gmail.com</p>
        </a>
        {/* SOCIAL ICONS GO HERE
                <ul className="footer__element__icon-list">
                    <li><Icon /></li>
                    <li><Icon /></li>
                    <li><Icon /></li>
                </ul>
                */}
      </div>
      <div className='footer__element'>
        <h3>Quick Links</h3>
        <ul className='footer__element__link-list'>
          {navbarItems.map((item, index) => {
            return (
              <li key={index + '_footer_item'}>
                <Link className='navbar__list__link' to={item.link}>
                  {item.label}
                </Link>
              </li>
            )
          })}
        </ul>
      </div>
      <div className='footer__mentions'>
        <p>created by </p>
        <strong>Rachid Amri</strong>
        <p>all rights reserved</p>
      </div>
    </section>
  )
}
